import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { useSelector, useDispatch } from "react-redux";
import { Button } from "./button";
import { FaTimes } from "react-icons/fa";

const DropDownContainer = styled.div`
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: #ec8d18;
  display: grid;
  align-items: center;
  top: 0;
  left: 0;
  transition: 0.3s ease-in-out;
  opacity: ${({ open }) => (open ? "1" : "0")};
  top: ${({ open }) => (open ? "0" : "-100%")};
`;

const Icon = styled.div`
  position: absolute;
  top: 1.2rem;
  right: 1.5rem;
  background: transparent;
  font-size: 2rem;
  cursor: pointer;
  outline: none;
`;
const CloseIcon = styled(FaTimes)`
  color: #000d1a;
`;
const DropdownWrapper = styled.div``;

const DropdownMenu = styled.div`
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 80px);
  text-align: center;
  margin-bottom: 4rem;

  @media screen and (max-width: 480px) {
    grid-template-rows: repeat(4, 60px);
  }
`;
const DropdownLink = styled(Link)`
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 1.5rem;
  text-decoration: none;
  list-style: none;
  cursor: pointer;
  transition: 0.2s ease-in-out;

  &:hover {
    color: #000d1a;
  }
`;
const BtnWrap = styled.div`
  display: flex;
  justify-content: center;
`;

const DropdownSidenav = ({ toggle, open }) => {
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;
  return (
    <DropDownContainer open={open} onClick={toggle}>
      <Icon>
        <CloseIcon onClick={toggle}></CloseIcon>
      </Icon>
      <DropdownWrapper>
        <DropdownMenu>
          <DropdownLink to='/'>Main Page</DropdownLink>
          <DropdownLink to='/cart'>Cart</DropdownLink>
        </DropdownMenu>

        {!userInfo && (
          <BtnWrap>
            <Button primary='true' round='true' big='true' to='/login'>
              Sign In
            </Button>
          </BtnWrap>
        )}
      </DropdownWrapper>
    </DropDownContainer>
  );
};

export default DropdownSidenav;
